import Container from "../components/container"
import * as React from "react"
import Header from "../components/header"
import { Link } from "gatsby"
import Img from "gatsby-image"
import Footer from "../components/footer"

export default function Home({data}) {
  return (
    <Container>
      <Header headerTitle="hometitle"></Header>
          <section className="hero">
            <div className="hero-body">
              <p className="title">
                #conti.photos
              </p>
              <p className="subtitle">
                a place for my photography, built over time with passion
              </p>
            </div>
          </section>
          <section className="section">
            {
              data.allFile.edges.map( 
                ({node}) => (
                  <div className="block" key={node.childImageSharp.id}>
                    <Img fluid={node.childImageSharp.fluid} />
                  </div>
                )
              )
            }
          </section>
      <section className="section">
        <div className="content">
          <h4 className="subtitle is-size-4">Latest collections</h4>
          <div className="columns">
            {
              data.allMarkdownRemark.edges.map( 
                ({node}) => (
                  <div className="column" key={node.id}>
                    <div className="box">
                      <article className="media">
                        <div className="media-left">
                          <figure className="image is-128x128">
                            <Img fixed={node.frontmatter.featuredImage.childImageSharp.fixed} />
                          </figure>
                        </div>
                        <div className="media-content">
                          <div className="content">
                            <h4>{node.frontmatter.title}</h4>
                            <h5>
                            <span className="is-size-6" style={{ color: `#999` }}>
                              <span className={`${node.frontmatter.type != 'primary' ? 'hide' : ''}`} >Last update: </span>
                              <span className={`${node.frontmatter.type == 'primary' ? 'hide' : ''}`} >Published on: </span>
                              {node.frontmatter.date}
                            </span>
                            </h5>
                            <Link to={node.fields.slug} className="button">
                                View more
                            </Link>
                          </div>
                        </div>
                      </article>
                    </div>
                  </div>
                )
              )
            }
          </div>
        </div>
      </section>
      <Footer></Footer>
    </Container>
  )
}

export const query = graphql`
  {
    allMarkdownRemark(sort: {order: DESC, fields: frontmatter___date}, limit: 2) {
      edges {
        node {
          frontmatter {
            title
            date
            type
            featuredImage {
              childImageSharp {
                fixed(width: 128, height: 128) {
                  aspectRatio
                  base64
                  height
                  originalName
                  src
                  srcSet
                  srcSetWebp
                  srcWebp
                  width
                }
              }
            }
          }
          fields {
            slug
          }
          id
        }
      }
    }
    allFile(sort: {fields: birthTime}, limit: 1, filter: {relativeDirectory: {eq: "photos"}}) {
      edges {
        node {
          childImageSharp {
            id
            fluid(maxWidth: 1280, maxHeight: 853, fit: CONTAIN, background: "white") {
              aspectRatio
              base64
              originalImg
              originalName
              presentationHeight
              presentationWidth
              sizes
              src
              srcSet
              srcSetWebp
              srcWebp
            }
          }
        }
      }
    }
  }
`